.center-content-container {
  display: flex;
}

.center-content-container .center-content-container__left {
  flex: 2;
}
.center-content-container .center-content-container__right {
  flex: 3;
  margin-left: 20px;
}

.center-content-container .center-content-container__right > *:not(:first-child) {
  margin-top: 17px;
}

@media screen and (max-width: 830px) {
  .center-content-container {
    flex-direction: column;
  }

  .center-content-container .center-content-container__right {
    margin-top: 17px;
    margin-left: 0;
  }
}
